import CacheHandle from './CacheHandle'
import {LOGIN_TOKEN} from '../common/constant/cacheTypes'

const Utils = {
  // 字符串 func 集合
  string: {
    isEmpty (str) {
      return !str || (typeof str === 'string' && !str.trim())
    }
  },
  // 日期 date func 集合
  formatDate (format, date = new Date()) {
    format = format.toLowerCase()
    if (format === 'yyyy-mm-dd') return `${date.getFullYear()}-${date.getMonth()}-${date.getDate()}`
  },
  // dom
  dom: {
    haveElement (element, parent) {
      if (!element || !parent) throw new ReferenceError('wrong parameters')
      else {
        let parentElement = element.parentElement
        if (parentElement === parent) return true
        else if (!parent || parentElement === document.documentElement) return false
        else return Utils.dom.haveElement(parentElement, parent)
      }
    }
  },
  // object
  object: {
    isObject (obj) {
      return obj && Object.prototype.toString.call(obj) === '[object Object]'
    }
  },
  developer: {
    login: false,
    isLogin () {
      if (this.loginState) return true
      else {
        let loginCache = CacheHandle.getItem(LOGIN_TOKEN)
        loginCache = JSON.parse(loginCache)
        if (loginCache && new Date().getTime() - parseInt(loginCache.expire, 10) < 10E5 * 864 && loginCache.data) return true
      }
    }
  }
}

export default Utils



