let CacheHandle = {
  setItem (key, value) {
    if (key && value) localStorage.setItem(key, value)
    else console.warn('params in CacheHandle.setItem is invalid, function canceled')
  },
  removeItem (key) {
    key && localStorage.removeItem(key)
  },
  getItem (key) {
    key && localStorage.getItem(key)
  },
  init () {
    // 本打算做兼容性处理，暂不做，留空
  }
}

export default CacheHandle



